* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

* li {
  list-style-type: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-family: "Roboto", sans-serif;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

._container {
  max-width: 1400px;
  padding: 0 15px;
  margin: 0 auto;
}

.sea {
  padding: 96px 230px;
}
.sea__row {
  position: relative;
  flex: 0 0 50%;
  display: flex;
  align-items: center;
}
.sea__row > :not(:last-child) {
  margin: 0 50px 0 0;
}
.sea__image img {
  max-width: 100%;
}
.sea__content {
  flex: 0 0 380px;
}
.sea__content h2 {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}
.sea__text {
  font-family: "Roboto";
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
  margin: 24px 0 32px 0;
}
.sea__btn {
  width: 128px;
  height: 48px;
}

.btn {
  background: #e4c1ae;
  border-radius: 32px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #ffffff;
  padding: 16px 40px;
  letter-spacing: 2px;
}
.btn:hover {
  background-color: #e15132;
  transition: background-color 0.5s linear;
  border-radius: 32px 5px 32px;
  transition: border-radius 0.5s linear;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal__dialog {
  max-width: 920px;
  margin: 100px auto;
}
.modal__content {
  position: relative;
  width: 100%;
  padding: 40px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  max-height: 80vh;
  overflow-y: auto;
  display: flex;
}
.modal__close {
  position: absolute;
  top: 8px;
  right: 14px;
  font-size: 30px;
  color: #000;
  opacity: 0.5;
  font-weight: 700;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.modal__title {
  font-size: 22px;
  text-transform: uppercase;
}
.modal__text {
  margin: 20px 0;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}

.show {
  display: block;
}

.hide {
  display: none;
}

.modal__reset {
  background: #e4c1ae;
  border-radius: 32px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #ffffff;
  padding: 16px 40px;
  letter-spacing: 2px;
}

@media (max-width: 1399px) {
  .sea {
    padding: 96px 30px;
  }
}